.shopping-cart p {
    margin: .75rem;
}

.shopping-cart {
    background-color: #3A5A40;
    color: #DAD7CD;
    font-weight: 600;
}

.shopping-cart__flex {
    display: flex;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
    font-size: 1.25rem;

}

.shopping-cart__push-right {
    margin-left: auto;

}

.underline {
    text-decoration: underline;
}

.shopping-cart__total {
    background-color: #DAD7CD;
    color: black;
    font-weight: 700;
}
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Courier New', Courier, monospace;
    background: #DAD7CD;
    color: black;
}

img {
    max-width: 100%;
    vertical-align: bottom;
}

input,
select,
button {
    cursor: pointer;
}

.container {
    width: 85%;
    max-width: 1100px;
    margin: 0 auto;
}
.home__background {
    min-height: 93vh;
    background-image: url("/shopping-cart/assets/priscilla-du-preez-rhva4q_n8CQ-unsplash.jpg");
    background-position: center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero__text {
    min-height: 100%;

}

.hero__title,
.hero__copy,
.hero__link {
    font-size: 1.5rem;
    text-align: right;
    margin-top: 0;
    color: #DAD7CD;
    background-color: #3a5a40b8;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    padding: .75em;
}

.hero__copy {
    text-align: left;
    font-size: 1.25rem;
}

.hero__link {
    margin: 0 auto;
    text-decoration: none;
}

.hero__link a {
    text-decoration: none;
}

.hero__link a:visited {
    color: #DAD7CD;
}

.hero__link a:hover {
    color: #dad7cd90;
}
.item__card {
    max-width: 100%;
    width: 300px;
    text-align: center;
}

.item__img {
    width: 300px;
    height: 300px;
    /* background-image: url('../assets/MagicalMysteryTourDoubleEPcover.jpg'); */
}

.item__quantity,
.item__add {
    font-family: inherit;
    font-size: 1rem;
    font-weight: 700;
    background-color: #A3B18A;
    padding: .25em;
    border: 1px solid #344E41;
    text-align: center;
    width: 3.5em;
    cursor: pointer;
}

.item__add {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: .25em .75em;
    margin-left: 1em;
}

.shop__items__flex {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    grid-gap: 2.5em;
    gap: 2.5em;
    padding: 3em 0;
}

.shop__item__quantity {}
* {
    box-sizing: border-box;
}

.nav {
    background-color: #3A5A40;
    color: #DAD7CD;
}



.nav__flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__title {
    font-size: 1.5rem;
}

a {
    text-decoration: none;
}

.nav__links {
    display: flex;
    align-items: center;
    list-style: none;
    grid-gap: 1em;
    gap: 1em;
}

.nav__link {
    color: black;
    font-size: 1.125rem;
    font-weight: 500;
    padding: .25em;
    background-color: #DAD7CD;
    border: .25rem solid #3A5A40;
}

.active {
    border: .25rem solid black;
}

.nav__link:hover {
    color: rgba(0, 0, 0, 0.67);
}

.selected {
    background-color: #DAD7CD;
}

.nav__cart {
    max-width: 30px;
    cursor: pointer;
}

.nav__cart-items {
    font-size: 1.125rem;
    margin-left: -.5em;
    font-weight: 600;
}

.nav__cart-items-background {
    /* background-color: #DAD7CD;
    color: black;
    padding: .25em;
    border-radius: 100%; */
    vertical-align: bottom;
}
